@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/averageColorBackground.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';

.root {
    --last-release-card-width: 200px;

    position: relative;

    padding: 0;
}

.averageColorBackground {
    @include averageColorBackground();
}

.header {
    padding-block-start: var(--ym-spacer-size-l);
    padding-block-end: var(--ym-spacer-size-xl);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding: var(--ym-spacer-size-l);
    }
}

.content {
    display: flex;
    flex-direction: column;

    justify-content: space-between;

    padding: 0;

    height: 100%;
}

.recents {
    display: flex;
    gap: calc(2 * var(--ym-spacer-size-xxxl));

    margin-block-end: var(--ym-spacer-size-xl);

    &:empty {
        margin: 0;
        padding: 0;
    }

    @include mediaBreakpointDown($ym-breakpoint-xl, false) {
        gap: var(--ym-spacer-size-xxxxl);
    }

    @include mediaBreakpointDown($ym-breakpoint-s, false) {
        flex-direction: column;

        gap: var(--ym-spacer-size-xxxl);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        gap: var(--ym-spacer-size-xxl);

        margin-block-end: var(--ym-spacer-size-xxl);
    }
}

.popularTracksBlock {
    flex-grow: 1;

    max-width: 100%;

    &_withLastRelease {
        max-width: 60%;

        @include mediaBreakpointDown($ym-breakpoint-s, false) {
            order: 2;

            max-width: 100%;
        }
    }
}

.popularTracksContainer {
    margin: 0;
    padding-inline-start: var(--ym-spacer-size-l);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-s);
    }
}

.tracksContainer {
    padding-inline-start: var(--ym-spacer-size-l);

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-l);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-xs);
    }
}

.lastReleaseBlock {
    @include mediaBreakpointDown($ym-breakpoint-s, false) {
        order: 1;

        padding-inline-start: var(--ym-spacer-size-xxl);
        padding-inline-end: var(--ym-spacer-size-s);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-l);
    }
}

.carouselHeader {
    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-l);
    }
}

// TODO: убрать дополнительный селектор после фикса https://st.yandex-team.ru/MFRONT-5960
.carouselContainer.important {
    margin: 0;
    padding-inline-start: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-l);
    }
}

.popularTracks {
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-l);
        padding-inline-end: var(--ym-spacer-size-l);
    }
}

// TODO: убрать дополнительный селектор после фикса https://st.yandex-team.ru/MFRONT-5960
.lastReleaseHeader.important {
    padding-inline-start: 0;
    padding-inline-end: 0;
}

.lastReleaseCard {
    width: var(--last-release-card-width);
    max-width: var(--last-release-card-width);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        --last-release-card-width: 200px;
    }

    @include mediaBreakpointDown(550, false) {
        --last-release-card-width: 160px;
    }
}

.carouselBlocks {
    display: flex;
    flex-direction: column;

    gap: var(--ym-spacer-size-s);

    &:empty {
        padding: 0;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        gap: var(--ym-spacer-size-xs);
    }
}

.footer {
    padding-block-start: var(--ym-spacer-size-xxxxl);
    padding-block-end: var(--ym-spacer-size-xxl);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-s);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-block-start: var(--ym-spacer-size-xxl);
        padding-block-end: var(--ym-spacer-size-xxxl);
        padding-inline-start: var(--ym-spacer-size-l);
    }
}
